import { NgModule } from '@angular/core'
import { RouterModule } from '@angular/router'
import { TranslateModule } from '@ngx-translate/core'

import {
    MatButtonModule,
    MatDialogModule,
    MatDividerModule,
    MatIconModule,
    MatInputModule,
    MatMenuModule,
    MatProgressBarModule,
    MatSelectModule,
    MatTooltipModule
} from '@angular/material'

import { FuseSharedModule } from '@fuse/shared.module'

import { NumberOnlyModule } from 'app/directive/numberOnly.module'

import { NgxPaginationModule } from 'ngx-pagination'
import { OrderModule } from 'ngx-order-pipe'

import { AuthGuard } from 'app/guard/guard-auth.guard'

import { ListVendorComponent } from './list-vendor/list-vendor.component'
import { DialogAddVendor } from './list-vendor/dialog/dialog-add-vendor/dialog-add-vendor'
import { DialogEditVendor } from './list-vendor/dialog/dialog-edit-vendor/dialog-edit-vendor'
import { DialogDeleteVendor } from './list-vendor/dialog/dialog-delete-vendor/dialog-delete-vendor'

const routes = [
    {
        path: '',
        component: ListVendorComponent,
        canActivate: [AuthGuard]
    }
]

@NgModule({
    declarations: [
        ListVendorComponent,
        DialogAddVendor,
        DialogEditVendor,
        DialogDeleteVendor
    ],
    entryComponents: [
        DialogAddVendor,
        DialogEditVendor,
        DialogDeleteVendor
    ],
    imports: [
        RouterModule.forChild(routes),

        TranslateModule,

        MatButtonModule,
        MatDialogModule,
        MatDividerModule,
        MatIconModule,
        MatInputModule,
        MatMenuModule,
        MatProgressBarModule,
        MatSelectModule,
        MatTooltipModule,

        FuseSharedModule,

        NumberOnlyModule,

        NgxPaginationModule,
        OrderModule
    ]
})

export class VendorModule { }