import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { UtilityService } from './utility.service';

@Injectable()
export class VendorsService {
    constructor(
        private _http: HttpClient,
        private utilityService: UtilityService
    ) { }

    loadListVendors() {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + this.utilityService.token
            })
        }

        return this._http
            .get(this.utilityService.server + "/api/vendors", httpOptions)
    }

    AddVendors(ParamReq: any) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + this.utilityService.token
            })
        }

        return this._http
            .post(this.utilityService.server + "/api/vendors/create", ParamReq, httpOptions)
    }

    EditVendors(ParamReq: any) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + this.utilityService.token
            })
        }

        return this._http
            .post(this.utilityService.server + "/api/vendors/update", ParamReq, httpOptions)
    }

    DeleteVendors(ParamReq: any) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + this.utilityService.token
            })
        }

        return this._http
            .post(this.utilityService.server + "/api/vendors/delete", ParamReq, httpOptions)
    }
}