import { Component, Inject, ViewEncapsulation } from "@angular/core"
import { fuseAnimations } from "@fuse/animations"
import { Router } from "@angular/router"
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material"
import { SubscriptionLike } from "rxjs"

import { UtilityService } from "app/services/utility.service"
import { VendorsService } from "app/services/vendors.service"

@Component({
    selector: 'dialog-delete-vendor',
    templateUrl: 'dialog-delete-vendor.html',
    providers: [
        UtilityService,
        VendorsService
    ],
    encapsulation: ViewEncapsulation.None,
    animations: fuseAnimations
})

export class DialogDeleteVendor {
    isShowMessage: boolean

    statusModal: string
    failMessage: string

    timeout: NodeJS.Timer
    subDeleteVendor: SubscriptionLike

    constructor(
        public dialogRef: MatDialogRef<DialogDeleteVendor>,
        @Inject(MAT_DIALOG_DATA)
        public data: any,
        private router: Router,

        private utilityService: UtilityService,
        private vendorsService: VendorsService
    ) {
        this.isShowMessage = false

        this.statusModal = "idle"
    }

    successCondition() {
        clearTimeout(this.timeout)

        this.statusModal = "success"
    }

    errorCondition(message: string) {
        clearTimeout(this.timeout)

        this.isShowMessage = true
        this.failMessage = message
        this.statusModal = "idle"
    }

    timeoutCondition(hitApi: any) {
        hitApi.unsubscribe()
        this.errorCondition("Timeout, Mohon periksa koneksi internet anda")
    }

    ngOnDestroy(): void {
        clearTimeout(this.timeout)

        if (this.subDeleteVendor) {
            this.subDeleteVendor.unsubscribe()
        }
    }

    DeleteVendorFunc() {
        this.statusModal = "loading"

        let ParamReq = {
            user_id: this.utilityService.user_id,
            vendor_id: this.data.id
        }

        this.subDeleteVendor = this.vendorsService.DeleteVendors(ParamReq)
            .subscribe((result: any) => {
                if (result.status == 200) {
                    clearTimeout(this.timeout)

                    this.statusModal = "success"
                } else {
                    this.errorCondition(result.message)
                }
            }, _error => {
                if (_error.status == 0) {
                    this.errorCondition("Maaf, tidak dapat menghubungkan ke server")
                } else if (_error.status == 400) {
                    this.router.navigate(['/login'])
                } else if (_error.status == 401) {
                    this.errorCondition(_error.error.message)
                } else {
                    this.errorCondition("Maaf, sepertinya telah terjadi kesalahan server, mohon coba beberapa saat lagi")
                }
            })

        this.timeout = setTimeout(() => {
            this.timeoutCondition(this.subDeleteVendor)
        }, 10000)
    }

    finishProcess() {
        this.dialogRef.close(200)
    }

}
