import { Component, ViewEncapsulation, OnInit } from '@angular/core'
import { fuseAnimations } from '@fuse/animations'
import { Router } from '@angular/router'
import { SubscriptionLike } from 'rxjs'

import { MatDialog } from '@angular/material'

import { UtilityService } from 'app/services/utility.service'
import { VendorsService } from 'app/services/vendors.service'

import { DialogAddVendor } from './dialog/dialog-add-vendor/dialog-add-vendor'

import { DialogEditVendor } from './dialog/dialog-edit-vendor/dialog-edit-vendor'

import { DialogDeleteVendor } from './dialog/dialog-delete-vendor/dialog-delete-vendor'

@Component({
    selector: 'list-vendor',
    templateUrl: './list-vendor.component.html',
    styleUrls: ['./list-vendor.component.scss'],
    providers: [
        UtilityService,
        VendorsService
    ],
    encapsulation: ViewEncapsulation.None,
    animations: fuseAnimations
})
export class ListVendorComponent implements OnInit {
    isLoading: boolean
    isSuccessFetch: boolean
    reverseListVendor: boolean

    orderListVendor: string
    itemListVendor: string
    failMessage: string

    pageListVendor: number

    filteredListVendor = []
    arrListVendor = []

    timeout: NodeJS.Timer
    subLoadListVendor: SubscriptionLike

    constructor(
        private router: Router,
        private dialog: MatDialog,

        private utilityService: UtilityService,
        private vendorsService: VendorsService
    ) {
        this.orderListVendor = "vendor_name"
        this.itemListVendor = "10"
        this.pageListVendor = 1
    }

    ngOnInit(): void {
        this.loadListVendor()
    }

    ngOnDestroy(): void {
        clearTimeout(this.timeout)

        if (this.subLoadListVendor) {
            this.subLoadListVendor.unsubscribe()
        }
    }

    timeoutCondition(hitApi: any) {
        hitApi.unsubscribe()
        clearTimeout(this.timeout)

        this.isLoading = false
        this.isSuccessFetch = false

        this.failMessage = "Timeout, Mohon periksa koneksi internet anda"
    }

    filterListVendor(filterValue: string) {
        filterValue = filterValue.trim()
        filterValue = filterValue.toUpperCase()

        this.arrListVendor = this.filteredListVendor.filter(item => item.vendor_name.includes(filterValue))
    }

    setOrderListVendor(value: string) {
        if (this.orderListVendor === value) {
            this.reverseListVendor = !this.reverseListVendor;
        }

        this.orderListVendor = value
    }

    loadListVendor() {
        this.isLoading = false
        this.isSuccessFetch = true

        this.isLoading = true

        this.subLoadListVendor = this.vendorsService.loadListVendors()
            .subscribe((result: any) => {
                clearTimeout(this.timeout)

                if (result.status == 200) {
                    this.filteredListVendor = result.data
                    this.arrListVendor = result.data

                    this.isLoading = false
                    this.isSuccessFetch = true
                } else {
                    this.isLoading = false
                    this.isSuccessFetch = false

                    this.failMessage = result.message
                }
            }, _error => {
                clearTimeout(this.timeout)

                this.isLoading = false
                this.isSuccessFetch = false

                if (_error.error.code == 0) {
                    this.failMessage = "Maaf, tidak dapat menghubungkan ke server"
                } else if (_error.status == 400) {
                    this.router.navigate(['/login'])
                } else {
                    this.failMessage = "Maaf, sepertinya telah terjadi kesalahan server, mohon coba beberapa saat lagi"
                }
            })

        this.timeout = setTimeout(() => {
            this.timeoutCondition(this.subLoadListVendor)
        }, 10000)
    }

    addVendorDialog() {
        let dialogRef = this.dialog.open(DialogAddVendor, {
            width: "500px",
            disableClose: true
        })

        dialogRef.afterClosed()
            .subscribe((result: number) => {
                if (result == 200) {
                    this.loadListVendor()
                }
            })
    }

    editVendorDialog(ListVendor: any) {
        let dialogRef = this.dialog.open(DialogEditVendor, {
            width: "500px",
            disableClose: true,
            data: {
                ListVendor: ListVendor
            }
        })

        dialogRef.afterClosed()
            .subscribe((result: number) => {
                if (result == 200) {
                    this.loadListVendor()
                }
            })
    }

    deleteVendorDialog(id: number, vendor_name: string) {
        let dialogRef = this.dialog.open(DialogDeleteVendor, {
            width: "500px",
            disableClose: true,
            data: {
                id: id,
                vendor_name: vendor_name
            }
        })

        dialogRef.afterClosed()
            .subscribe((result: number) => {
                if (result == 200) {
                    this.loadListVendor()
                }
            })
    }
}