import { Component, Inject, ViewEncapsulation } from "@angular/core"
import { fuseAnimations } from "@fuse/animations"
import { Router } from "@angular/router"
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material"
import { FormGroup, FormControl, FormBuilder } from "@angular/forms"
import { SubscriptionLike } from "rxjs"

import { UtilityService } from "app/services/utility.service"
import { VendorsService } from "app/services/vendors.service"

@Component({
    selector: 'dialog-edit-vendor',
    templateUrl: 'dialog-edit-vendor.html',
    providers: [
        UtilityService,
        VendorsService
    ],
    encapsulation: ViewEncapsulation.None,
    animations: fuseAnimations
})

export class DialogEditVendor {
    isShowMessage: boolean

    statusModal: string
    failMessage: string

    EditVendorForm: FormGroup

    vendor_name = new FormControl('')
    phone_number = new FormControl('')
    company_name = new FormControl('')
    npwp = new FormControl('')
    bank_name = new FormControl('')
    account_bank = new FormControl('')
    account_bank_name = new FormControl('')

    timeout: NodeJS.Timer
    subEditVendor: SubscriptionLike

    constructor(
        public dialogRef: MatDialogRef<DialogEditVendor>,
        @Inject(MAT_DIALOG_DATA)
        public data: any,
        private router: Router,
        private formBuilder: FormBuilder,

        private utilityService: UtilityService,
        private vendorsService: VendorsService
    ) {
        this.isShowMessage = false

        this.statusModal = "idle"

        this.vendor_name.setValue(this.data.ListVendor.vendor_name)
        this.phone_number.setValue(this.data.ListVendor.phone_number)
        this.company_name.setValue(this.data.ListVendor.company_name)
        this.npwp.setValue(this.data.ListVendor.npwp)
        if (this.data.ListVendor.bank_name != null) {
            this.bank_name.setValue(this.data.ListVendor.bank_name)
        }
        if (this.data.ListVendor.account_bank != null) {
            this.account_bank.setValue(this.data.ListVendor.account_bank)
        }
        if (this.data.ListVendor.account_bank_name != null) {
            this.account_bank_name.setValue(this.data.ListVendor.account_bank_name)
        }

        this.EditVendorForm = this.formBuilder.group({
            vendor_name: this.vendor_name,
            phone_number: this.phone_number,
            company_name: this.company_name,
            npwp: this.npwp,
            bank_name: this.bank_name,
            account_bank: this.account_bank,
            account_bank_name: this.account_bank_name
        })
    }

    successCondition() {
        clearTimeout(this.timeout)

        this.statusModal = "success"
    }

    errorCondition(message: string) {
        clearTimeout(this.timeout)

        this.isShowMessage = true
        this.failMessage = message
        this.statusModal = "idle"
    }

    timeoutCondition(hitApi: any) {
        hitApi.unsubscribe()
        this.errorCondition("Timeout, Mohon periksa koneksi internet anda")
    }

    ngOnDestroy(): void {
        clearTimeout(this.timeout)

        if (this.subEditVendor) {
            this.subEditVendor.unsubscribe()
        }
    }

    EditVendorFunc() {
        this.statusModal = "loading"

        let ParamReq = {
            user_id: this.utilityService.user_id,
            vendor_id: this.data.ListVendor.id,
            vendor_name: this.vendor_name.value.toUpperCase(),
            phone_number: this.phone_number.value,
            company_name: this.company_name.value.toUpperCase(),
            npwp: this.npwp.value,
            bank_name: this.bank_name.value.toUpperCase(),
            account_bank: this.account_bank.value,
            account_bank_name: this.account_bank.value.toUpperCase()
        }

        this.subEditVendor = this.vendorsService.EditVendors(ParamReq)
            .subscribe((result: any) => {
                if (result.status == 200) {
                    clearTimeout(this.timeout)

                    this.statusModal = "success"
                } else {
                    this.errorCondition(result.message)
                }
            }, _error => {
                if (_error.status == 0) {
                    this.errorCondition("Maaf, tidak dapat menghubungkan ke server")
                } else if (_error.status == 400) {
                    this.router.navigate(['/login'])
                } else if (_error.status == 401) {
                    this.errorCondition(_error.error.message)
                } else {
                    this.errorCondition("Maaf, sepertinya telah terjadi kesalahan server, mohon coba beberapa saat lagi")
                }
            })

        this.timeout = setTimeout(() => {
            this.timeoutCondition(this.subEditVendor)
        }, 10000)
    }

    finishProcess() {
        this.dialogRef.close(200)
    }

}
